<template>
  <div class="render-menu" v-if="!LOADING">
    <router-link to="/" class="homepage">Home Page</router-link>
    <router-link
      :to="{ name: 'genres', params: { genres: item.name }}"
      v-for="item in genres"
      :key="item.id"
      :class=" $route.params.genres === item.name ? 'menuActive' : '' "
      v-colorChange
    >#{{item.name}}</router-link>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
  computed: {
    ...mapState({
      LOADING: state => state.menu.loading
    }),
    ...mapGetters({
      genres: "menu/getGenres"
    })
  },
  mounted() {
    this.$store.dispatch("menu/fetchGenres");
  }
};
</script>

